          <template #cell(certificationDate)="data"
            >{{ data.item.certificationDate }}
          </template>
          <template #cell(certificationSigned)="data"
            >{{ data.item.certificationSigned }}
          </template>
        </b-table>

        <b-pagination
          align="center"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="table"
        ></b-pagination>
        <span>{{ errorMessage }}</span>
      </div>
    </div>
  </div>
</template>

