<template>
  <div class="page">
    <div class="content list-editor">
      <form action="">
        <b-table
          id="table"
          class="table-content"
          table-variant="light"
          :fields="fields"
          :items="this.institutions"
          :per-page="perPage"
          :current-page="currentPage"
          ref="table"
        >
          <template #cell(institution)="data">
            {{ data.item.institution }}

            <b-icon-trash-fill
              class="floated-right"
              @click="Remove(data.index)"
            ></b-icon-trash-fill>
          </template>
        </b-table>
        <b-pagination
          variant="dark"
          class="pagination-place"
          align="center"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="table"
        ></b-pagination>
        <span>{{ errorMessage }}</span>
        <div class="inline-elements">
          <b-icon-plus-circle
            class="h4 mb-2 plus-icon"
            shift-v="-8"
            @click="Add"
            >Intézmény hozzáadása</b-icon-plus-circle
          >
          <input
            type="text"
            class="list-editor-input"
            v-model="element"
            placeholder="Új elem hozzáadása"
            v-on:keyup.enter="Add"
          />
        </div>
        <b-form-select></b-form-select>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "InstitutionListEditor",
  data() {
    return {
      fields: [{ key: "institution", label: "Intézmények", sortable: true }],
      institutions: [
        { institution: "Kandó" },
        { institution: "Avasi" },
        { institution: "Földes" },
        { institution: "Herman" },
        { institution: "Bláthy" },
        { institution: "Andrássy" },
        { institution: "Kandó" },
        { institution: "Avasi" },
        { institution: "Földes" },
        { institution: "Herman" },
        { institution: "Bláthy" },
        { institution: "Andrássy" },
        { institution: "Kandó" },
        { institution: "Avasi" },
        { institution: "Földes" },
        { institution: "Herman" },
        { institution: "Bláthy" },
        { institution: "Andrássy" },
      ],
      perPage: 10,
      currentPage: 1,
      errorMessage: "",
      element: "",
    };
  },
  computed: {
    rows() {
      return this.institutions.length;
    },
  },
  methods: {
    Add() {
      if (this.element != "") {
        this.institutions.push({ institution: this.element });
        this.$refs.table.refresh();
        this.element = "";
      }
    },
    Edit() {},
    Remove(index) {
      this.$delete(this.institutions, index);
      this.$refs.table.refresh();
    },
  },
};
</script>

<style></style>
