<template>
  <div class="page">
    <div class="content list-editor">
      <form action="">
        <b-table
          id="table"
          class="table-content"
          table-variant="light"
          :fields="fields"
          :items="this.list"
          :per-page="perPage"
          :current-page="currentPage"
          ref="table"
        >
          <template #cell(examGrade)="data">
            {{ data.item.examGrade
            }}<b-icon-trash-fill
              class="floated-right"
              @click="Remove(data.index)"
            ></b-icon-trash-fill>
          </template>
        </b-table>
        <b-pagination
          variant="dark"
          class="pagination-place"
          align="center"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="table"
        ></b-pagination>
        <span>{{ errorMessage }}</span>
        <div class="inline-elements">
          <b-icon-plus-circle
            class="h4 mb-2 plus-icon"
            shift-v="-8"
            @click="Add"
          ></b-icon-plus-circle>
          <input
            type="text"
            class="list-editor-input"
            v-model="element"
            placeholder="Új elem hozzáadása"
          />
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "ExamGradeListEditor",

  data() {
    return {
      fields: [{ key: "examGrade", label: "Vizsga jegy", sortable: true }],
      list: [
        { examGrade: "Elégtelen" },
        { examGrade: "Elégséges" },
        { examGrade: "Megfelelt" },
        { examGrade: "Jó" },
        { examGrade: "Kiváló" },
      ],
      perPage: 10,
      currentPage: 1,
      errorMessage: "",
      element: "",
    };
  },
  computed: {
    rows() {
      return this.list.length;
    },
  },
  methods: {
    Add() {
      this.list.push({ institution: this.element });
      this.$refs.table.refresh();
    },
  },
};
</script>
